.hero {
  .container__hero {
    @include m.grid(1, 1fr, auto, 1fr, 2rem);
    @include m.tablet {
      @include m.grid(2, 1fr, auto, 1fr, 2rem);
    }
    .contendor-texto {
      display: flex;
      flex-direction: column;
      justify-content: center;
      h2 {
        text-align: center;
        font-size: 3.5rem;
        line-height: 4rem;
        @include m.tablet {
          text-align: start;
          font-size: 4rem;
          line-height: 5rem;
        }
        margin: 2rem 0rem;
        text-transform: uppercase;
      }
      p {
        font-size: 2.5rem;
        text-align: center;
        @include m.tablet {
          font-size: 3rem;
          line-height: 5rem;
          text-align: start;
        }
        color: v.$gris;
        line-height: 4rem;
      }
      .textop__hero {
        background-color: v.$celeste;
        color: v.$white;
        text-transform: uppercase;
        text-align: center;
        line-height: 3.5rem;
        font-size: 2rem;
        padding: 1rem 3rem;
        font-weight: 700;
        &:hover {
          background-color: lighten(v.$white, 0.1);
          color: v.$bgcolor;
        }
      }
    }
    .contenedor-img {
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        object-fit: cover;
      }
    }
  }
}

.section-tittle {
  h2 {
    text-align: center;
    font-size: 4rem;
    margin: 5rem 1rem;
    text-transform: uppercase;
  }
  p {
    color: v.$gris;
    margin: 2rem 0rem;
    text-align: center;
    line-height: 2.5rem;
  }

  // tambien afecta "funciones"

  .container-box {
    @include m.grid(1, 1fr, 3, 1fr, 5rem);
    @include m.tablet {
      @include m.grid(2, 1fr, 2, 1fr, 5rem);
    }
    @include m.pc{
      @include m.grid(4, 1fr, 1, 1fr, 5rem);
    }
    margin: 2rem 0rem;
  }
  .box {
    img {
      filter: contrast(110%);
    }
    h3 {
      margin-top: 1rem;
      text-transform: capitalize;
      text-align: center;
      font-size: 2rem;
    }
    h2 {
      font-size: 6rem;
      @include m.pc {
        font-size: 8rem;
      }
    }
    p {
      color: v.$gris;
      margin-top: 2rem;
      text-align: center;
      line-height: 2.5rem;
    }
  }
}

// PROYECTOS OVERLAY

.container-box__galeria {
  @include m.grid(1, 1fr, 3, 1fr, 5rem);
  @include m.tablet {
    @include m.grid(2, 1fr, 1, 1fr, 5rem);
  }
  @include m.pc{
    @include m.grid(4, 1fr, 1, 1fr, 5rem);
    
  }

  .box__galeria {
    position: relative;

    .texto__galeria {
      p {
        display: none;
      }
      position: absolute;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;

      transition: all 0.3s;
      &:hover {
        p {
          display: inline-block;
          text-transform: uppercase;
          font-weight: 700;
          font-size: 2rem;
          color: v.$white;
          opacity: 1;
        }
        background-color: v.$grisTransparent;
      }
    }
  }
}

// ACERCA DE

.acercadeTittle {
  text-align: center;
  margin: 5rem 0rem;
  text-transform: uppercase;
}

.contenedor-acercade {
  @include m.grid(1, 1fr, auto, 1fr, 5rem);
  @include m.tablet {
    @include m.grid(2, 1fr, auto, 1fr, 5rem);
    align-items: center;
  }
  .texto-acercade {
    text-align: center;
    text-transform: capitalize;
    p {
      line-height: 4rem;
      color: v.$gris;
    }
  }
  .img-acercade {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 1rem;
    h3{
      text-align: center;
      text-transform: capitalize;
    }
    .container__acercadeflex {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      .box__acercade {
        margin: 2rem;
        position: relative;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        &:hover {
          .logo__acercade {
            bottom: -3rem;
            opacity: 1;
          }
        }
        .logo__acercade {
          position: absolute;
          bottom: -4rem;
          opacity: 0;
          transition: 0.2s;
          p {
            font-size: 2.5rem;
            color: v.$white;
            // display: none;
            text-transform: capitalize;
            font-weight: 700;
          }
        }

        .fa-bootstrap,
        .fa-git-alt,
        .fa-github,
        .fa-js,
        .fa-sass,
        .fa-html5,
        .fa-css3-alt,
        .fa-react,
        .fa-gulp {
          font-size: 10rem;
        }

        .fa-git-alt {
          color: #f1502f;
        }
        .fa-github {
          color: #000000;
        }
        .fa-js {
          color: #f0db4f;
        }
        .fa-sass {
          color: #cd6799;
        }
        .fa-html5 {
          color: #e34c26;
        }
        .fa-css3-alt {
          color: #2565ae;
        }
        .fa-bootstrap {
          color: #553c7b;
        }
        .fa-react {
          color: #61dbfb;
        }
        .fa-gulp {
          color: #db4446;
        }
      }
    }
  }
}

.section-tittle {
  .h2__funciones {
    font-size: 10rem;
  }
  p {
    line-height: 3rem;
    text-align: center;
  }
}

// CONTACTO

.container-address {
  @include m.grid(1, 1fr, auto, 1fr, 5rem);
  @include m.tablet {
    @include m.grid(3, 1fr, auto, 1fr, 5rem);
  }
  .address {
    h4,
    p {
      text-align: center;
      @include m.tablet {
        text-transform: capitalize;
        text-align: left;
      }
    }
  }
}

.form {
  max-width: 50rem;
  h4 {
    text-align: center;
    margin: 2rem 1rem;
  }
  fieldset {
    border: none;
    display: flex;
    flex-direction: column;
    .section__form {
      width: 100%;
      padding: 2rem;

      label {
        text-transform: capitalize;
        color: v.$white;
      }
      input {
        display: block;
        width: 100%;
        padding: 1rem;
        outline: none;
      }
    }
    label {
      text-transform: capitalize;
      color: v.$white;
    }
    textarea {
      resize: none;
      outline: none;
    }
  }
  .button__enviar {
    margin-top: 2rem;
    background-color: v.$white;
    padding: 1rem 2rem;
    border: none;
    color: v.$bgcolor;
    font-weight: 700;
    transition: 0.5s;
    &:hover {
      cursor: pointer;
      background-color: darken($color: v.$white, $amount: 50);
    }
  }
}

// ICONOS TABLET

.iconos__tablet {
  display: none;
  @include m.tablet {
    position: fixed;
    right: 0;
    display: flex;
    flex-direction: column;
    a {
      color: v.$white;
      font-size: 3rem;
      padding: 1rem 2rem;

      transition: 1s;
      &:hover {
        color: v.$azul;
      }
    }
  }
}

// ARROW UP

.arrow-up {
  z-index: 1;
  position: fixed;
  bottom: 0;
  right: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 3rem;

  width: 8rem;
  height: 8rem;

  margin: 0;
  @include m.tablet {
    margin: 0 3rem 10rem 0;
  }
  opacity: 0;
  transition: 0.5s;

  .fa-arrow-up {
    align-items: center;
    color: v.$white;
  }
}

.active__arrowUp {
  display: flex;
  opacity: 1;
}
