html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "exo+2", sans-serif;
  font-size: 1.6rem;
  line-height: 2;
}

/* globales */
* {
  text-decoration: none;
}

h1,
h2,
h3,
h4 {
  font-family: "exo+2", sans-serif;
  line-height: 1.2;
  margin: 0;
}

h1 {
  font-size: 4.8rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3.2rem;
}

h4 {
  font-size: 2.8rem;
}

p {
  line-height: 1.5rem;
  font-size: 2rem;
  color: #000000;
  margin: 1rem 0rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

/* utilidades */
.container {
  max-width: 120rem;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    max-width: 60rem;
  }
}
@media (min-width: 1025px) {
  .container {
    max-width: 120rem;
  }
}

.container__v2 {
  max-width: 100rem;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container__v2 {
    max-width: 60rem;
  }
}
@media (min-width: 1025px) {
  .container__v2 {
    max-width: 120rem;
  }
}

body::-webkit-scrollbar {
  width: 1rem;
  background-color: #27374D;
}

body::-webkit-scrollbar-thumb {
  background-color: #526D82;
}

