.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
}
.nav a {
  color: #000000;
}
.nav a h1 {
  padding: 2.5rem;
  margin: 0;
  text-transform: capitalize;
  font-size: 2.5rem;
}

.nav__ul {
  z-index: 1;
  position: absolute;
  top: -100vh;
  left: 0;
  width: 100%;
  height: calc(100vh + 5rem);
  background-color: #DDE6ED;
  opacity: 0;
  transition: 1s;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .nav__ul {
    position: inherit;
    width: auto;
    opacity: 1;
    background-color: transparent;
    top: 0;
    height: auto;
    flex-direction: row;
  }
}
.nav__ul li a {
  transition: 0.5s;
  padding: 2rem;
  font-size: 3rem;
  text-transform: capitalize;
  color: #27374D;
}
@media (min-width: 768px) {
  .nav__ul li a {
    color: #DDE6ED;
  }
}
.nav__ul li a:hover {
  color: #9DB2BF;
}
@media (min-width: 768px) {
  .nav__ul li a {
    font-size: 2rem;
  }
}
.nav__ul .fa-facebook,
.nav__ul .fa-instagram,
.nav__ul .fa-twitter,
.nav__ul .fa-github {
  color: #27374D;
  font-size: 3rem;
  padding: 1rem 2rem;
}
.nav__ul .fa-facebook:hover,
.nav__ul .fa-instagram:hover,
.nav__ul .fa-twitter:hover,
.nav__ul .fa-github:hover {
  color: #9DB2BF;
}
@media (min-width: 768px) {
  .nav__ul .fa-facebook,
  .nav__ul .fa-instagram,
  .nav__ul .fa-twitter,
  .nav__ul .fa-github {
    display: none;
  }
}

.desactivarScroll {
  overflow: hidden;
}

.activarScroll {
  overflow: scroll;
}

.activeBtn {
  overflow: hidden;
  top: 0;
  opacity: 1;
}

.button {
  margin-right: 2rem;
  z-index: 2;
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  position: relative;
}
@media (min-width: 768px) {
  .button {
    display: none;
  }
}

span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #DDE6ED;
  width: 100%;
  height: 0.3rem;
  display: block;
  border-radius: 0.5rem;
  transition: 0.5s;
}

.l1 {
  transform: translate(-50%, 0.6rem);
}

.l3 {
  transform: translate(-50%, -0.9rem);
}

.activeBtn .l1 {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.activeBtn .l2 {
  width: 0;
}

.activeBtn .l3 {
  transform: translate(-50%, -50%) rotate(45deg);
}

.activeBtn span {
  background-color: #000000;
}

h1 {
  color: #DDE6ED;
}

h2,
h3,
h4 {
  color: #DDE6ED;
}

