.hero .container__hero {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(auto, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .hero .container__hero {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(auto, 1fr);
    gap: 2rem;
  }
}
.hero .container__hero .contendor-texto {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.hero .container__hero .contendor-texto h2 {
  text-align: center;
  font-size: 3.5rem;
  line-height: 4rem;
  margin: 2rem 0rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .hero .container__hero .contendor-texto h2 {
    text-align: start;
    font-size: 4rem;
    line-height: 5rem;
  }
}
.hero .container__hero .contendor-texto p {
  font-size: 2.5rem;
