@use "./variables" as v;

@mixin tablet {
  @media (min-width: v.$tablet) {
    @content;
  }
}

@mixin pc {
  @media (min-width: v.$pc) {
    @content;
  }
}

@mixin grid($columns, $medidaC, $rows, $medidaR, $gap) {
  display: grid;
  grid-template-columns: repeat($columns, $medidaC);
  grid-template-rows: repeat($rows, $medidaR);
  gap: $gap;
}

@mixin resetearUl {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin button(
  $bgColor: #000000,
  $fontColor: #ffffff,
  $fontWeight: 700,
  $fontZise: 2rem,
  $padding: 1rem 3rem
) {
  background-color: bgColor;
  color: fontColor;
  font-weight: fontWeight;
  padding: padding;
  font-size: fontZise;
  text-decoration: none;
  text-align: center;
  display: inline-block;
  border: none;
  &:hover {
    cursor: pointer;
    color: v.$gris;
  }
}
