@use "base/variables" as v;
@use "base/mixins" as m;

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  a {
    color: v.$black;
    h1 {
      padding: 2.5rem;
      margin: 0;
      text-transform: capitalize;
      font-size: 2.5rem;
    }
  }
  position: relative;
}
.nav__ul {
  z-index: 1;
  position: absolute;
  top: -100vh;
  left: 0;

  width: 100%;
  height: calc(100vh + 5rem);

  background-color: v.$white;

  opacity: 0;
  transition: 1s;

  @include m.resetearUl;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @include m.tablet {
    position: inherit;
    width: auto;
    opacity: 1;
    background-color: transparent;
    top: 0;
    height: auto;

    flex-direction: row;
  }
  li {
    a {
      transition: 0.5s;
      padding: 2rem;
      font-size: 3rem;
      text-transform: capitalize;
      color: v.$bgcolor;
      @include m.tablet{
        color: v.$white;
      }
      &:hover {
        color: v.$azul;
      }
      @include m.tablet {
        font-size: 2rem;
      }
    }
  }
  .fa-facebook,
  .fa-instagram,
  .fa-twitter,
  .fa-github {
    color: v.$bgcolor;
    font-size: 3rem;
    padding: 1rem 2rem;
    &:hover {
      color: v.$azul;
    }
    @include m.tablet {
      display: none;
    }
  }
}

.desactivarScroll {
  overflow: hidden;
}

.activarScroll{
  overflow: scroll;
}

.activeBtn {
  overflow: hidden;
  top: 0;
  opacity: 1;
}

// button

.button {
  margin-right: 2rem;
  z-index: 2;
  width: 3rem;
  height: 3rem;
  cursor: pointer;

  position: relative;
  @include m.tablet {
    display: none;
  }
}

span {
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);

  background-color: v.$white;
  width: 100%;
  height: 0.3rem;

  display: block;
  border-radius: 0.5rem;

  transition: 0.5s;
}

.l1 {
  transform: translate(-50%, 0.6rem);
}
.l3 {
  transform: translate(-50%, -0.9rem);
}
// js
.activeBtn .l1 {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.activeBtn .l2 {
  width: 0;
}
.activeBtn .l3 {
  transform: translate(-50%, -50%) rotate(45deg);
}
.activeBtn span{
  background-color: v.$black;
}